<template>
   <div class="BxLoader">
      <v-app>
         <v-dialog 
            v-model="dialogLoder" 
            width="350" persistent 
            content-class="PersonalLoading"
            overlay-opacity="0.7"
         >
            <v-card>
               <v-card-text class="text-xs-center">
               <BlueLoader />
               </v-card-text>
            </v-card>
         </v-dialog>
      </v-app>
   </div>
  
</template>

<script>
   export default {
      components:{
         BlueLoader : () => import ("../Loader/lottie/GreenLoader")
      },
      data(){
         return{
            dialogLoder: true,
         }
      }
   }
</script>

<style lang="scss">
	@import './Loader.scss';
</style>



